import React from "react";
import { PanelHeader, PanelHeaderBack, Group, Button } from "@vkontakte/vkui";

class TypeCollection extends React.Component {
  render() {
    return (
      <React.Fragment>
        <PanelHeader
          left={
            <PanelHeaderBack
              onClick={() => {
                this.props.changePanel("main");
              }}
            />
          }
        >
          Тип сбора
        </PanelHeader>

        <Group separator="hide" onClick={() =>{
          this.props.changePanel('target-collection')
        }}>
          <div style={{ height: 96 }}>
            <div>Целевой сбор</div>
            <div>Когда есть определенная цель</div>
          </div>
        </Group>
        <Group separator="hide">
          <div style={{ height: 96 }}>
            Регулярный сбор Если помощь нужна ежемесячно
          </div>
          <Button
            mode="primary"
            onClick={() => {
              this.props.changePanel("regular-collection");
            }}
          >
            Подробнее
          </Button>
        </Group>
      </React.Fragment>
    );
  }
}

export default TypeCollection;
