import React from "react";
import { View, Panel } from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";
import MainPanel from "./panels/main";
import TargetCollection from "./panels/targetCollection";
import TypeCollection from "./panels/typeCollection";
import RegularCollection from "./panels/regularCollection";

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activePanel: "main",
    };
  }
  changePanel = (panel) =>
    this.setState({
      activePanel: panel,
    });
  render() {
    return (
      <View activePanel={this.state.activePanel}>
        <Panel id="main">
          <MainPanel changePanel={this.changePanel} />
        </Panel>
        <Panel id="type-collection">
          <TypeCollection changePanel={this.changePanel} />
        </Panel>
        <Panel id="target-collection">
          <TargetCollection changePanel={this.changePanel} />
        </Panel>
        <Panel id="regular-collection">
          <RegularCollection changePanel={this.changePanel} />
        </Panel>
      </View>
    );
  }
}

export default App;
