import React from "react";
import { PanelHeader, PanelHeaderBack, Group } from "@vkontakte/vkui";

class RegularCollection extends React.Component {
  render() {
    return (
      <React.Fragment>
        <PanelHeader
          left={
            <PanelHeaderBack
              onClick={() => {
                this.props.changePanel("type-collection");
              }}
            />
          }
        >
          Регулярный сбор
        </PanelHeader>
        <Group>
          <div>Загрузите обложку</div>
        </Group>
      </React.Fragment>
    );
  }
}

export default RegularCollection;
