import React from "react";
import { PanelHeader, Header, Group, Button } from "@vkontakte/vkui";

class MainPanel extends React.Component {
  render() {
    return (
      <React.Fragment>
        <PanelHeader>Пожертвования</PanelHeader>
        <Group header={<Header mode="secondary">Items</Header>}>
          <div>У вас пока нет сборов.</div>
          <div>Начните доброе дело.</div>
          <Button mode="primary" onClick={() => { this.props.changePanel('type-collection') }}>Создать сбор</Button>
        </Group>
      </React.Fragment>
    );
  }
}

export default MainPanel