import React from "react";
import { PanelHeader, PanelHeaderBack, FormLayout, FormLayoutGroup, Input } from "@vkontakte/vkui";

class TargetCollection extends React.Component {
  render() {
    return (
      <React.Fragment>
        <PanelHeader
          left={
            <PanelHeaderBack
              onClick={() => {
                this.props.changePanel("type-collection");
              }}
            />
          }
        >
          Целевой сбор
        </PanelHeader>
        <FormLayout>
          <FormLayoutGroup
            top="Пароль"
            bottom="Пароль может содержать только латинские буквы и цифры."
          >
            <Input type="password" placeholder="Введите пароль" />
            <Input type="password" placeholder="Повторите пароль" />
          </FormLayoutGroup>
        </FormLayout>
      </React.Fragment>
    );
  }
}

export default TargetCollection;
